import React, { useEffect } from 'react'

const Toolbar = (props) => {

    const toogleContrast = (initial = false) => {
        if (window.sessionStorage) {
            const contrast = "contrast"
            const current = window.sessionStorage.getItem(contrast)

            if (!initial || (initial && current === undefined)) {
                window.sessionStorage.setItem(contrast, !JSON.parse(current || true))
            }
        }
    }

    useEffect(() => {
        toogleContrast(true)
    }, [])

    return (
        <React.Fragment>
            <div className="app-toolbar">
                <div className="app-toolbar--item">A+</div>
                <div className="app-toolbar--item">A-</div>
                <div className="app-toolbar--item" onClick={toogleContrast}>Wersja kontrastowa</div>
            </div>
        </React.Fragment>
    )
}

export default Toolbar